import React from 'react'
import Dashboard from './Dashboard'
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Navbar from './Navbar';
import Library from './Library';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link
} from "react-router-dom";

const Home = () => {
  

  return (
    <div>
    <Router>
    <Box sx={{ flexGrow: 1 }}>
      <Grid container spacing={2}>
        <Navbar/>
        {/* main content start */}
        <Grid item className="sidebar" xs={2} >
            <div className="sidebarOptions" ><Link to='dorabhai-educator-panel/' > Dashboard</Link></div>
            <div className="sidebarOptions" ><Link to='dorabhai-educator-panel/library' > Library</Link></div>
        </Grid>

    <Routes>
          <Route exact path='dorabhai-educator-panel/library' element={<Library/>} /> 
          <Route exact path="dorabhai-educator-panel/" element={<Dashboard/>} />
    </Routes>
        
      </Grid>
    </Box>
    </Router>
    </div>
  )
}

export default Home